html, body {
  width: 400px !important;
  height: 600px !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  box-sizing: border-box !important;
}

/* Ensure the root element takes full popup dimensions */
#root {
  width: 400px !important;
  height: 600px !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  box-sizing: border-box !important;
}

/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  font-size: 14px;
  line-height: 1.4;
  color: #333;
  background: #f8f9fa;
}

/* Popup container */
.popup-container {
  width: 400px !important;
  height: 600px !important;
  min-width: 400px !important;
  min-height: 600px !important;
  max-width: 400px !important;
  max-height: 600px !important;
  background: white;
  border: 3px solid #007bff !important; /* Blue border to make dimensions visible */
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  box-sizing: border-box;
}

/* Add a size indicator for debugging */
.popup-container::before {
  content: '400600';
  position: absolute;
  top: 5px;
  right: 5px;
  background: #007bff;
  color: white;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 10px;
  font-weight: bold;
  z-index: 1000;
}

/* Loading state */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 400px;
  padding: 40px;
}

.loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #e3e3e3;
  border-top: 3px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Error state */
.error-container {
  padding: 40px;
  text-align: center;
}

.error-icon {
  font-size: 48px;
  margin-bottom: 16px;
}

.error-container h3 {
  margin-bottom: 12px;
  color: #dc3545;
}

.retry-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  margin-top: 16px;
}

.retry-button:hover {
  background: #0056b3;
}

/* Authentication container */
.auth-container {
  padding: 32px;
}

/* Form styles */
.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  margin-bottom: 6px;
  font-weight: 500;
  color: #495057;
}

.form-input {
  width: 100%;
  padding: 12px;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 14px;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.form-input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.form-input.error {
  border-color: #dc3545;
}

.form-input:disabled {
  background: #f8f9fa;
  cursor: not-allowed;
}

.error-message {
  display: block;
  margin-top: 6px;
  font-size: 12px;
  color: #dc3545;
}

.general-error {
  display: flex;
  align-items: center;
  padding: 12px;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 6px;
  margin-bottom: 20px;
  color: #721c24;
}

.error-icon {
  margin-right: 8px;
  font-size: 16px;
}

/* Button styles */
.login-button,
.signup-button {
  width: 100%;
  padding: 12px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.login-button:hover,
.signup-button:hover {
  background: #0056b3;
}

.login-button:disabled,
.signup-button:disabled {
  background: #6c757d;
  cursor: not-allowed;
}

.link-button {
  background: none;
  border: none;
  color: #007bff;
  text-decoration: underline;
  cursor: pointer;
  font-size: 14px;
}

.link-button:hover {
  color: #0056b3;
}

.link-button:disabled {
  color: #6c757d;
  cursor: not-allowed;
}

/* Form headers and footers */
.login-header,
.signup-header {
  text-align: center;
  margin-bottom: 32px;
}

.login-header h2,
.signup-header h2 {
  margin-bottom: 8px;
  color: #212529;
  font-size: 24px;
}

.login-header p,
.signup-header p {
  color: #6c757d;
  font-size: 14px;
}

.login-footer,
.signup-footer {
  text-align: center;
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #e9ecef;
}

/* Lockout message */
.lockout-message {
  text-align: center;
  padding: 40px 20px;
}

.lockout-icon {
  font-size: 48px;
  margin-bottom: 16px;
}

.lockout-message h3 {
  margin-bottom: 12px;
  color: #dc3545;
}

.lockout-timer {
  margin-top: 20px;
}

.timer-bar {
  width: 100%;
  height: 8px;
  background: #e9ecef;
  border-radius: 4px;
  overflow: hidden;
}

.timer-progress {
  height: 100%;
  background: #dc3545;
  transition: width 1s linear;
}

/* Main interface styles */
.main-interface {
  padding: 24px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid #e9ecef;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.user-avatar {
  width: 40px;
  height: 40px;
  background: #007bff;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 16px;
}

.user-details h3 {
  font-size: 16px;
  margin-bottom: 2px;
  color: #212529;
}

.user-email {
  font-size: 12px;
  color: #6c757d;
}

.logout-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: background-color 0.2s;
}

.logout-button:hover {
  background: #f8f9fa;
}

.logout-icon {
  font-size: 16px;
}

/* Extension content */
.extension-content h2 {
  margin-bottom: 8px;
  color: #212529;
  font-size: 20px;
}

.extension-content p {
  margin-bottom: 20px;
  color: #6c757d;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 24px;
  padding: 12px;
  background: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 6px;
  color: #155724;
}

.status-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.status-dot.connected {
  background: #28a745;
}

.quick-actions {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}

.action-button {
  flex: 1;
  padding: 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s;
}

.action-button.primary {
  background: #007bff;
  color: white;
}

.action-button.primary:hover {
  background: #0056b3;
}

.action-button.secondary {
  background: #6c757d;
  color: white;
}

.action-button.secondary:hover {
  background: #545b62;
}

.info-section {
  background: #f8f9fa;
  padding: 16px;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.info-section h4 {
  margin-bottom: 12px;
  color: #212529;
  font-size: 14px;
}

.info-section ol {
  padding-left: 20px;
  color: #6c757d;
  font-size: 13px;
}

.info-section li {
  margin-bottom: 6px;
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .popup-container {
    width: 100%;
    min-height: 100vh;
    border-radius: 0;
  }
  
  .auth-container {
    padding: 24px 20px;
  }
  
  .main-interface {
    padding: 20px;
  }
}
